import React from 'react';
import CircularProgress from '@mui/material/CircularProgress';
import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableCell from '@mui/material/TableCell';
import TableContainer from '@mui/material/TableContainer';
import TableHead from '@mui/material/TableHead';
import TableRow from '@mui/material/TableRow';
import Paper from '@mui/material/Paper';
import Stack from '@mui/material/Stack';
import Button from '@mui/material/Button';
import TextField from '@mui/material/TextField';
import './style.css';
import { Link } from 'react-router-dom';
import useFetchTodos from '../../customHooks/useFetchTodos';

function TodoList() {
  const {
    todos,
    loading,
    todoDeleted,
    editableTodos,
    activeTodo,
    handleTodoClick,
    handleDeleteClick,
    handleSaveClick,
    handleEditClick,
    handleEditInputChange,
  } = useFetchTodos();

  return (
    <div className="todo__list">
      <Link to="/create">
        <Button variant="outlined">Add Todo</Button>
      </Link>
      {loading ? (
        <CircularProgress />
      ) : (
        <TableContainer component={Paper}>
          <Table aria-label="simple table">
            <TableHead>
              <TableRow>
                <TableCell>Todo name</TableCell>
                {/* <TableCell align="right">Completed</TableCell> */}
                <TableCell align="left">Actions</TableCell>
              </TableRow>
            </TableHead>
            <TableBody>
              {todos.map((todo) => (
                <TableRow
                  key={todo.id}
                  sx={{ '&:last-child td, &:last-child th': { border: 0 } }}
                  className={`todo ${
                    activeTodo === todo.id ? 'todo--active' : ''
                  }`}
                  onClick={() => handleTodoClick(todo.id)}
                >
                  {editableTodos[todo.id]?.editing ? (
                    <>
                      <TableCell>
                        <TextField
                          id={`edit-title-${todo.id}`}
                          label="Edit Title"
                          variant="outlined"
                          type="text"
                          value={editableTodos[todo.id]?.title}
                          onChange={(event) =>
                            handleEditInputChange(todo.id, event.target.value)
                          }
                        />
                      </TableCell>
                      <TableCell align="right">
                        <Button
                          variant="outlined"
                          onClick={(event) => handleSaveClick(event, todo.id)}
                        >
                          Save
                        </Button>
                      </TableCell>
                    </>
                  ) : (
                    <>
                      <TableCell>{todo.title}</TableCell>
                      <TableCell align="right">
                        <Stack spacing={2} direction="row">
                          <Button
                            variant="outlined"
                            // onClick={() => handleEditClick(todo.id)}
                          >
                            View
                          </Button>
                          <Button
                            variant="outlined"
                            onClick={(event) =>
                              handleEditClick(event, todo.id, todo.title)
                            }
                          >
                            Edit
                          </Button>
                          <Button
                            variant="outlined"
                            onClick={() => handleDeleteClick(todo.id)}
                          >
                            Delete
                          </Button>
                        </Stack>
                      </TableCell>
                    </>
                  )}
                </TableRow>
              ))}
            </TableBody>
          </Table>
        </TableContainer>
      )}
      {todoDeleted && (
        <div className="todo__delete__message">Todo deleted successfully!</div>
      )}
    </div>
  );
}

export default TodoList;
