import { useState, useEffect } from 'react';
import { fetchToDos, deleteToDo, updateToDo } from '../services/todoService';

const useFetchTodos = () => {
  const [todos, setTodos] = useState([]);
  const [loading, setLoading] = useState(true);
  const [todoDeleted, setTodoDeleted] = useState(false);
  const [editableTodos, setEditableTodos] = useState({});
  const [activeTodo, setActiveTodo] = useState({
    id: '',
    title: '',
    completed: false,
  });

  useEffect(() => {
    fetchTodos();
  }, []);

  // fetch data via service - GET
  const fetchTodos = async () => {
    try {
      const data = await fetchToDos();

      setTodos(data);
      setLoading(false);
    } catch (error) {
      console.error('Error fetching data:', error);
      setLoading(false);
    }
  };

  // delete data via service - DELETE
  const deleteTodo = async (id) => {
    try {
      await deleteToDo(id);

      setTodoDeleted(true);
      fetchTodos();
    } catch (error) {
      console.error('Error deleting data:', error);
    }
  };

  // handle save click via service - PUT
  const handleSaveClick = async (event, id) => {
    event.stopPropagation();

    const editedTodo = editableTodos[id];

    try {
      await updateToDo(id, editedTodo.title);

      updateTodos((prevTodos) =>
        prevTodos.map((todo) =>
          todo.id === id ? { ...todo, title: editedTodo.title } : todo
        )
      );

      setEditableTodos((prevEditableTodos) => ({
        ...prevEditableTodos,
        [id]: { ...prevEditableTodos[id], editing: false },
      }));
    } catch (error) {
      console.error('Error updating data:', error);
    }
  };

  // handle delete click via service
  const handleDeleteClick = (id) => {
    deleteTodo(id);
  };

  // update todos state
  const updateTodos = (newTodos) => {
    setTodos(newTodos);
  };

  // handle edit click via service
  const handleEditClick = (event, id, title) => {
    event.stopPropagation();

    setEditableTodos((prevEditableTodos) => ({
      ...prevEditableTodos,
      [id]: {
        ...prevEditableTodos[id],
        editing: !prevEditableTodos[id]?.editing,
        title,
      },
    }));
  };

  const updateLoading = (newLoading) => {
    setLoading(newLoading);
  };

  const refetchTodos = () => {
    setLoading(true);

    fetchTodos();
  };

  const handleTodoClick = (id) => {
    setActiveTodo(id);
  };

  const handleEditInputChange = (id, value) => {
    setEditableTodos((prevEditableTodos) => ({
      ...prevEditableTodos,
      [id]: { ...prevEditableTodos[id], title: value },
    }));
  };

  return {
    todos,
    loading,
    todoDeleted,
    editableTodos,
    activeTodo,
    updateTodos,
    updateLoading,
    refetchTodos,
    handleTodoClick,
    setTodoDeleted,
    setEditableTodos,
    deleteTodo,
    handleDeleteClick,
    handleSaveClick,
    handleEditClick,
    handleEditInputChange,
  };
};

export default useFetchTodos;
